import { useState } from 'react';

export function useForm(validateForm, inputValues = {}) {
  const [values, setValues] = useState(inputValues);

  const handleChange = (event) => {
    const { value, name } = event.target;

    setValues({ ...values, [name]: value });
    validateForm(event);
  };

  return {
    values,
    handleChange,
    setValues,
  };
}
